import React from 'react';
import ReactDOM from 'react-dom';

const SearchForm = (props) => (
  <form>
    <input id="input" value={props.searchVal} onChange={props.handleChange} />
    <button id="search" onClick={props.handleSubmit}>Search</button>
  </form>
);

const DirectoryView = (props) => (
  <div id="cardView">
    <SearchForm 
            value={props.searchVal}
            handleChange={props.handleChange}
            handleSubmit={props.handleSubmit}
            handleReset={props.handleReset}
    />
    {props.recipeList.map((recipe, i) => (
            <RecipeCard
              id={recipe.id}
              name={recipe.name} 
              ingredients={recipe.ingredients}
              instructions={recipe.instructions}
              key={i} 
              clickHandler={props.clickHandler}
            />))}
  </div>
);

const RecipeCard = (props) => (
  <div className='recipe'>
    <h2  onClick={() => props.clickHandler(props.id)}>{props.name}</h2>
  </div>
);

const DetailView = (props) => (
  <div id="detail">
    <h1>{props.recipe.name}</h1>
    <p>ingredients: {props.recipe.ingredients}</p>
    <p>instructions: {props.recipe.instructions}</p>
  </div>
);

class App extends React.Component{
  state = {
    recipes: [
      { id: 0, name: 'Harry Potter', ingredients: 'JK Rowling', instructions: 'a'},
      { id: 1, name: 'Universe in a Nutshell', ingredients: 'Stephen Hawking', instructions: 'b'},
      { id: 2, name: 'Design Principles', ingredients: 'Gang of Four', instructions: 'c'},
      { id: 3, name: 'Lexicon', ingredients: 'Max Barry', instructions: 'd'},
      { id: 4, name: 'The Codex', ingredients: 'Lev Grossman', instructions: 'e'},
      { id: 5, name: 'Song of Solomon', ingredients: 'Toni Morrison', instructions: 'f'},
    ],
    selectedRecipe: {},
    searchVal: ''
  }

  selectRecipe = (id) => {
    console.log('selected');
    console.log(id);
    this.setState({selectedRecipe: this.state.recipes[id]});
  }

  handleChange = (event) => {
    this.setState({ searchVal: event.target.value });
  }

  handleSubmit = (event) => {
    event.preventDefault();
    let recipesFiltered = this.state.recipes.filter(recipe => recipe.name === this.state.searchVal);
    if(this.state.searchVal === ''){
      recipesFiltered = this.state.recipes;
    }
    this.setState({ recipes: recipesFiltered });
  }

  render(){
    return (
      <div id="base">
          <DirectoryView recipeList={this.state.recipes} clickHandler={this.selectRecipe} searchVal={this.state.searchVal}  handleChange={this.handleChange} handleSubmit={this.handleSubmit}/>
          <DetailView recipe={this.state.selectedRecipe}/>
      </div>
    )
  }
}

ReactDOM.render(<App />, document.getElementById("root"));